<template>
	<Modal v-model="isShowEdit" title="新增">
		<Form ref="formValidate" :model="formItem" :rules="AddModalruleValidate" :label-width="80">
			<Form-item label="个人照" prop="photo">
				<Input v-model="formItem.photo" placeholder="请输入图片链接"></Input>
			</Form-item>
			<Form-item label="姓名" prop="name">
				<Input v-model="formItem.name" placeholder="请输入个人姓名"></Input>
			</Form-item>
			<Form-item label="身高(cm)" prop="height">
				<Input v-model="formItem.height" placeholder="请输入您的身高"></Input>
			</Form-item>
			<Form-item label="职业" prop="job">
				<Input v-model="formItem.job" placeholder="请输入您的身高"></Input>
			</Form-item>
			<Form-item label="微信" prop="wechat">
				<Input v-model="formItem.wechat" placeholder="请输入您的联系方式"></Input>
			</Form-item>
			<Form-item label="性别" prop="male">
				<Radio-group v-model="formItem.male">
					<Radio label="1">男</Radio>
					<Radio label="2">女</Radio>
				</Radio-group>
			</Form-item>
			<Form-item label="出生日期" prop="birthday">
				<Date-picker type="date" placeholder="选择出生日期" v-model="formItem.birthday"></Date-picker>
			</Form-item>
			<Form-item label="年收入(k)" prop="yearIncome">
				<Input v-model="formItem.yearIncome" placeholder="请输入您的年收入"></Input>
			</Form-item>
			<Form-item label="家乡" prop="hometown">
				<Cascader :data="cityList" v-model="formItem.hometown"></Cascader>
			</Form-item>
			<Form-item label="所在地" prop="localtion">
				<Cascader :data="cityList" v-model="formItem.localtion"></Cascader>
			</Form-item>
			<Form-item label="个人介绍" prop="introduction">
				<Input v-model="formItem.introduction" type="textarea" :autosize="{minRows: 2,maxRows: 5}"
					placeholder="请输入..."></Input>
			</Form-item>
			<Form-item>
				<Button type="primary" @click="handleSubmit('formValidate')">提交</Button>
				<Button style="margin-left: 8px">取消</Button>
			</Form-item>
		</Form>
		<div slot="footer"></div>
	</Modal>
</template>

<script>
	import {
		AddModalruleValidate,
		Dayjs
	} from "../data.js"
	export default {
		props: {
			isShowEdit: {
				type: Boolean,
				required: true,
			},
			cityList: {
				type: Array
			}

		},
		watch: {
			isShowEdit(newVal, oldVal) {
				if (!newVal) {
					this.closeEModal()
				}
			}
		},
		data() {
			return {
				formItem: {
					photo: 'https://img1.baidu.com/it/u=3251142218,4227433679&fm=253&fmt=auto&app=138&f=JPEG?w=509&h=500',
					name: '憨豆',
					height: '170',
					job: '演员',
					wechat: 'sss',
					male: '1',
					birthday: '',
					yearIncome: '20',
					hometown: [
						"110000","110100","110102"
					],
					localtion: ["110000","110100","110102"],
					introduction: "a后回家啊时间多久啊啥的就把精神的悲剧啊时间大把时间的吧"
				},
				AddModalruleValidate
			}
		},
		methods: {
			//关闭
			closeEModal() {
				this.$emit('closeEModal')
			},
			//提交保存
			handleSubmit(name) {
				this.$refs[name].validate((valid) => {
					if (valid) {
						fetch('http://localhost:5200/add/person',{
							method: 'POST',
							  headers: {
							    'Content-Type': 'application/json'
							  },
							  body: JSON.stringify({
							    ...this.formItem,
								birthday:Dayjs(this.formItem.birthday).valueOf()
							  })
						}).then((resp)=>{
							return resp.json();
						}).then((res)=>{
							this.cityList = res.data
						})
						this.$Message.success('提交成功!');
					} else {
						this.$Message.error('表单验证失败!');
					}
				})
			},
		}
	}
</script>

