const dayjs = require('dayjs')
export const Dayjs = dayjs;
export function columns(cityList) {
	return [{
			title: '个人照',
			key: 'photo',
			render:(h,{row})=>{
				return h("img",{attrs:{src:row.photo},style:{width:'90px',height:'120px'}});
			}
		},
		{
			title: '姓名',
			key: 'name'
		},
		{
			title: '身高(cm)',
			key: 'height'
		},
		{
			title: '职业',
			key: 'job'
		},
		{
			title: '微信ID',
			key: 'wechat'
		},
		{
			title: '性别',
			key: 'male',
			render:(h,{row})=>{
				return h("div",row.male==1?'男':'女');
			}
		},
		{
			title: '出生日期',
			key: 'birthday',
			render:(h,{row})=>{
				return h("div",dayjs(row.birthday).format('YYYY-MM-DD'));
			}
		},
		{
			title: '年收入(w)',
			key: 'yearIncome'
		},
		{
			title: '家乡',
			key: 'hometown',
			render:(h,{row})=>{
				const place = new fromatPlaceObj({cityList,codeList:row.hometown})
				return h("div",place.res);
			}
		},
		{
			title: '所在地',
			key: 'localtion',
			render:(h,{row})=>{
				const place = new fromatPlaceObj({cityList,codeList:row.localtion})
				return h("div",place.res);
			}
		},
		{
			title: '个人介绍',
			key: 'introduction'
		}
	]
}

// 格式化行政区域
class fromatPlaceObj{
	constructor(arg) {
	    this.res = '';
		this.$cityList = arg.cityList;
		this.$codeList = arg.codeList;
		this.$dep = 0;
		this.fromatPlace(this.$cityList,this.$codeList,this.$dep)
	}
	
	 fromatPlace(cityList,codeList,dep = 0){
		cityList.some((item)=>{
			if(item.value==codeList[dep]){
				this.res+=item.label;
				if(item.children){
					dep ++ ;
					this.fromatPlace(item.children,codeList,dep)
				}
			}
		})
	}
}



