<template>
	<div id="app">
		<div id="header_panel">
			<div id="left_header_panel">
				<div class="filter_item">
					<div class="label">性别：</div>
					<Radio-group v-model="filterParams.male">
						<Radio :label="1">男</Radio>
						<Radio :label="2">女</Radio>
					</Radio-group>
				</div>
				<div class="filter_item">
					<div class="label">出生范围：</div>
					<Date-picker type="year" placeholder="选择年" v-model="filterParams.ageStart"></Date-picker>
					～
					<Date-picker type="year" placeholder="选择年" v-model="filterParams.ageEnd"></Date-picker>
				</div>
				<div class="filter_item">
					<div class="label">身高范围：</div>
					<Input v-model="filterParams.heightStart" placeholder="请输入..." style="width: 70px"></Input>
					～
					<Input v-model="filterParams.heightEnd" placeholder="请输入..." style="width: 70px"></Input>
				</div>
				<div class="filter_item">
					<div class="label">年收入(k)：</div>
					<Input v-model="filterParams.yearIncomeStart" placeholder="请输入..." style="width: 70px"></Input>
					～
					<Input v-model="filterParams.yearIncomeEnd" placeholder="请输入..." style="width: 70px"></Input>
				</div>
				<div class="filter_item">
					<div class="label">家乡：</div>
					<Cascader :data="cityList" v-model="filterParams.hometown"></Cascader>
				</div>
				<div class="filter_item">
					<div class="label">当前城市：</div>
					<Cascader :data="cityList" v-model="filterParams.localtion"></Cascader>
				</div>
			</div>
			<div id="right_header_paenl">
				<i-button id="search_btn" type="info" @click="getPeopleList">搜索</i-button>
				<i-button type="info" @click="()=>isShowEdit = true">新增</i-button>
			</div>

		</div>
		<div id="contatin_panel">
			<Table :columns="columns" :data="peopleList"></Table>
			<div id="pagination">
				<Page :total="pageTotal" show-elevator></Page>
			</div>
		</div>
		<AddModal :isShowEdit="isShowEdit" :cityList="cityList" @closeEModal="closeEModal"></AddModal>
	</div>
</template>

<script>
	import {columns,data1} from './data.js'
	import AddModal from "./components/addModal.vue"
	const dayjs = require('dayjs')
	export default {
		name: 'app',
		components:{
			AddModal
		},
		data() {
			return {
				columns:[],
				filterParams: {
					male: 0,
					ageStart:'',
					ageEnd:'',
					heightStart:'',
					heightEnd:'',
					yearIncomeStart:'',
					yearIncomeEnd:'',
					hometown:[],
					localtion:[]
				},
				cityList:[],
				peopleList:[],
				isShowEdit:false,
				page:1,
				size:10,
				pageTotal:0
			}
		},
		created(){
			this.getCityList();
			this.getPeopleList()
		},
		methods:{
			//获取城市列表
			getCityList(){
				fetch('http://localhost:5200/cityList').then((resp)=>{
					return resp.json();
				}).then((res)=>{
					this.cityList = res.data;
					this.columns = columns.call(this,res.data)
				})
			},
			// 关闭弹窗
			closeEModal(){
				this.isShowEdit = false;
			},
			//获取人员列表
			getPeopleList(){
				console.log({...this.filterParams})
				fetch('http://localhost:5200/person/list',{
					method: 'POST',
					  headers: {
					    'Content-Type': 'application/json'
					  },
					  body: JSON.stringify({
					    ...this.filterParams,
						ageStart:this.filterParams.ageStart?dayjs(this.filterParams.ageStart).valueOf():'',
						ageEnd:this.filterParams.ageStart?dayjs(this.filterParams.ageEnd).valueOf():'',
						page:this.page,
						size:this.size
					  })
				}).then((resp)=>{
					return resp.json();
				}).then((res)=>{
					this.peopleList = res.data;
					this.pageTotal = res.total;
				})
			}
		}
	}
</script>

<style>
	body {
		padding: 0;
		margin: 0;
	}

	#app {
		font-family: 'Avenir', Helvetica, Arial, sans-serif;
		-webkit-font-smoothing: antialiased;
		-moz-osx-font-smoothing: grayscale;
		text-align: center;
		color: #2c3e50;
		padding: 0;
	}

	#header_panel {
		padding: 16px;
		box-sizing: border-box;
		background-color: #f6f6f6;
		display: flex;
		justify-content: space-between;
	}

	#left_header_panel {
		display: flex;
		align-items: center;
	}

	#left_header_panel>.filter_item {
		display: flex;
		align-items: center;
		margin-right: 20px;
	}

	#left_header_panel>.filter_item>.label {
		width: 80px;
	}
	#search_btn{
		margin-right: 16px;
	}
	#contatin_panel {
		margin: 20px;
	}
	#pagination {
		margin-top: 20px;
		display: flex;
		justify-content: flex-end;
	}
</style>
